export function mul(vector, scalar) {
    return {
        x: vector.x * scalar,
        y: vector.y * scalar
    };
}
export function add(v1, v2) {
    return {
        x: v1.x + v2.x,
        y: v1.y + v2.y
    };
}
export function neg(vector) {
    return {
        x: -vector.x,
        y: -vector.y
    };
}
export function sub(v1, v2) {
    return add(v1, neg(v2));
}
export function norm(vector) {
    return Math.sqrt(Math.pow(vector.x, 2) + Math.pow(vector.y, 2));
}
export function unit(vector) {
    return mul(vector, 1 / norm(vector));
}
export function vec(norm, angle) {
    return {
        x: Math.cos(angle) * norm,
        y: Math.sin(angle) * norm
    };
}
export function ang(vector) {
    return Math.atan2(vector.y, vector.x);
}
