import Vue from 'vue';
import Action from "./Action.vue";
import { Tabs } from "@/components/Tab";
import global from "@/store/global";
export default Vue.extend({
    data: function () {
        return {
            tabs: Tabs,
            global: global
        };
    },
    methods: {
        isTab: function (tab) {
            return global.tab === tab;
        },
        switchTab: function (tab) {
            global.tab = tab;
        }
    },
    created: function () {
        global.tab = Tabs.Run;
    },
    components: { Action: Action },
});
