import Vue from 'vue';
import _ from "lodash";
import IconBtn from "@/components/IconBtn.vue";
import Tape from "@/shared/Tape";
import run from "@/store/run";
import simulator, { Event } from '@/shared/simulator';
export default Vue.extend({
    components: { IconBtn: IconBtn },
    data: function () {
        return {
            run: run,
            tape: null,
            transition: null,
            resetTape: null,
        };
    },
    computed: {
        status: function () { return run.status; },
        playing: function () { return run.playing; },
        stepThrottle: function () {
            return _.throttle(function () { return run.step(); }, simulator.interval, { trailing: false });
        },
    },
    methods: {
        play: function () { return run.play(); },
        pause: function () { return run.pause(); },
        back: function () { return run.back(); },
        reset: function () { return run.reset(); },
        step: function () { return run.step(); },
    },
    mounted: function () {
        var _this = this;
        this.tape = new Tape(this.$refs.tape);
        // Sync when resetting
        this.resetTape = function () {
            console.log("resize");
            _this.tape.reset();
        };
        simulator.bus.$on([Event.BACK, Event.RESET], this.resetTape);
        // Animate transition
        this.transition = function (t) { return _this.tape.transition(t); };
        simulator.bus.$on(Event.TRANSITION, this.transition);
        window.addEventListener("resize", this.resetTape);
    },
    destroyed: function () {
        // Once we close our tape, we can halt the simulator
        run.pause();
        // Stop listening for simulator events
        simulator.bus.$off([Event.UPDATE, Event.BACK], this.resetTape);
        simulator.bus.$off(Event.TRANSITION, this.transition);
        // Stop listening for window resizes (we no longer need to resize our tape after it's destroyed)
        window.removeEventListener("resize", this.resetTape);
    }
});
