import Vue from "vue";
import store from "@/store";
import App from "./App.vue";
/**
 * Automatically focus.
 */
Vue.directive('focus', {
    inserted: function (el) {
        el.focus();
    }
});
/**
 * Automatically select.
 */
Vue.directive("select", {
    inserted: function (el) {
        el.select();
    }
});
/**
 * Automatically select when focusing.
 */
Vue.directive("selectOnFocus", {
    inserted: function (el) {
        el.addEventListener("focus", function () { return el.select(); });
    }
});
new Vue({
    store: store,
    el: '#main',
    template: "<App/>",
    components: { App: App },
});
