import Vue from 'vue';
import Field from "@/components/SideBar/Field.vue";
import Section from "@/components/SideBar/Section.vue";
import Icon from "@/components/Icon.vue";
import Toggle from "@/components/Toggle.vue";
import Input from "@/components/Input.vue";
import { Direction } from "@/shared/types";
import global from "@/store/global";
export default Vue.extend({
    data: function () {
        return {
            global: global,
            directions: [Direction.Left, Direction.Stay, Direction.Right]
        };
    },
    computed: {
        states: function () {
            return global.model.states;
        },
        transition: function () {
            return this.$store.state.table.transition;
        }
    },
    methods: {
        changeState: function (e) {
            var index = e.target.selectedIndex;
            this.transition.to = this.states[index];
            this.update();
        },
        update: function () {
            this.$store.state.table.table.update();
        },
        onWriteChange: function () {
            if (this.transition.write.length === 0) {
                this.transition.write = '#';
            }
            this.update();
        },
        setDirection: function (dir) {
            this.transition.direction = dir;
            this.update();
        },
        setUndefined: function (event) {
            this.update();
        }
    },
    components: { Section: Section, Icon: Icon, Input: Input, Toggle: Toggle, Field: Field }
});
