import { Model } from "@/shared/model";
import { Direction } from "@/shared/types";
export default (function () {
    var model = new Model();
    var a = {
        label: "a",
        position: { x: 0, y: 0 }
    };
    var b = {
        label: "b",
        position: { x: 200, y: 0 }
    };
    var c = {
        from: a,
        to: a,
        direction: Direction.Right,
        read: "1",
        write: "1"
    };
    var d = {
        from: a,
        to: b,
        direction: Direction.Right,
        read: "#",
        write: "#"
    };
    model.addState(a);
    model.addState(b);
    model.start = a;
    model.accept.add(b);
    model.addTransition(c);
    model.addTransition(d);
    return model;
});
